.container {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 115px;
    padding-right: 115px;
    @media screen and (min-width: 480px) {
      width: 320px;
    }
    @media screen and (min-width: 768px) {
      width: 595px;
    }
    @media screen and (min-width: 1280px) {
      width: 1050px;
    }
  }